}
.ant-carousel .slick-slide {
    text-align: center;
    height: 500px;
    line-height: 160px;
    background: rgb(44, 33, 33, 0.5);
    overflow: hidden;
  }
  .slide1 img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 5%;
    height: 5%;
  }
  
  .ant-carousel .slick-slide h3 {
    color: rgb(214, 29, 29);
  }